<template>
  <div id="Comment">
    <BookInfo :book="book"></BookInfo>
    <div>
      <el-form :model="form" ref="myForm" :rules="rules">
        <el-form-item label="完了日">
          <el-date-picker v-model="form.read" placeholder="完了日"></el-date-picker>
        </el-form-item>
        <el-form-item label="コメント" prop="comment">
          <el-input v-model="form.comment" placeholder="コメント" type="textarea"></el-input>
        </el-form-item>
        <el-button type="primary" @click="onsubmit">送信</el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
import BookInfo from "../components/BookInfo";
import { mapGetters, mapActions } from "vuex";
import { UPDATE_BOOK, UPDATE_CURRENT } from "../mutation-types";

export default {
  name: "Comment",
  components: {
    BookInfo
  },
  data() {
    return {
      book: {},
      form: {
        id: "",
        read: new Date(),
        comment: ""
      },
      rules: {
        comment: [
          { required: true, message: "内容を入力くだいさい。", trigger: "blur" }
        ]
      }
    };
  },
  computed: {
    ...mapGetters(["current"])
  },
  created() {
    if (!this.current) {
      this.$router("/");
    }
    //現在の本を設定
    this.book = Object.assign({}, this.current);
    this.form.id = this.book.id;
    this.form.read = this.book.read;
    this.form.comment = this.book.comment;
  },
  methods: {
    ...mapActions([UPDATE_BOOK, UPDATE_CURRENT]),
    onsubmit: function() {
      this.$refs["myForm"].validate(valid => {
        if (valid) {
          this[UPDATE_BOOK](Object.assign({}, this.form, this.book));
          this[UPDATE_CURRENT](null);
          this.$notify({
            title: "完了",
            message: "",
            duration: 2000,
            type: "success"
          });

          this.form.comment = "";
          this.form.read = new Date();
          this.$router.push("/");
        } else {
          return false;
        }
      });
    }
  }
};
</script>